export default {
  data () {
    return {
      isShow: true,
      hasAnimate: true
    }
  },
  computed: {
    /**
     * 
     * @return {String} 
     */
    animateName () {
      return this.hasAnimate ? 'opacity' : ''
    }
  },
  methods: {
    /**
     * 
     */
    opemAnimate () {
      this.hasAnimate = true
    },
    /**
     * 
     * @return {Promise} promise
     */
    removeAnimate () {
      return new Promise((resolve) => {
        this.hasAnimate = false
        resolve()
      })
    },
    /**
     * loading
     */
    show () {
      this.isShow = true
    },
    /**
     * loading
     */
    hide () {
      this.isShow = false
    }
  }
}



